/* ===========================================================

	# Heading

=========================================================== */

.o-heading {
	margin-bottom: $line-height;

	&:after {
		@include breakline();
	}

}

.o-heading__headline {
	font-weight: $bolder;
	padding-bottom: 0;
}

.o-heading__subtitle {
	opacity: .65;
	font-size: 1.4rem;
	display: block;
	font-style: italic;
	margin-top: 1rem;
}

.o-heading--center {
	text-align: center;

	&:after {
		margin-left: auto;
		margin-right: auto;
	}
}
